.sidebar {
  height: 100vh ;
  /* border-radius: 5px; */
  background-image: linear-gradient(135deg, #000000, #2f5f68); 
  /* background-color: black; */
  /* box-shadow: 0 0 2px gray; */
  color: white;
}


.menu {
  padding-top: 100px;
}

.menu-item {
  padding-top: 30px;
}

.menu-item a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.menu-item i {
  font-size: 1.2rem;
  margin: 0 15px;
  color: white;
}

.active {
  background-color: rgb(255, 236, 236);
  padding: 10px;
  margin-top: 25px;
}

.active a,
.active i {
  color: #07255a;
}
